<template>
  <div class="toolbar">
    <div class="tool">
      <div class="xz">
        <btn class="btn" :cheked="chekedAll" @click.native="ischekedAll"></btn>
        <span @click="ischekedAll">全选</span>
      </div>
      <div class="price">
        <span>合计:{{ toolPrice }}</span>
      </div>
      <div class="settlement" @click="settlement">
        <span>去结算({{ chekedLength }})</span>
      </div>
    </div>
  </div>
</template>

<script>
import btn from "../childcart/btn"
export default {
  components: {
    btn
  },
  computed: {
    toolPrice() {
      return (
        "￥" +
        this.$store.state.cartList
        .filter(value =>  value.cheked)
        .reduce((preValue, value) => preValue += value.lowNowPrice * value.count, 0)
        .toFixed(2)
      )
    },
    // 选中的个数
    chekedLength() {
      return this.$store.state.cartList.filter(item => item.cheked).length
    },
    // 全选
    chekedAll() {
      // 没有商品时
      if (this.$store.state.cartList.length == 0) return false
      // 只要有一个不选中,全选就不会选中
      return !this.$store.state.cartList.find(value => !value.cheked)
    }
  },
  methods: {
    ischekedAll() {
      console.log(this.chekedAll);
      if (this.chekedAll) {
        // 如果原来都是选中点击一次全部不选中
        this.$store.state.cartList.forEach(value => value.cheked = false)
      } else {
        // 部分选中和全部不选中时点击一次全部选中
        this.$store.state.cartList.forEach(value => value.cheked = true)
      }
    },
    settlement() {
      if (!this.chekedLength) {
        // 调用toast插件里面的函数
        this.$toast.show("至少选择一件商品！", 1500)
      } else {
        this.$toast.show("结算成功 ✔", 1500)
        this.$store.dispatch('clear')
      }
    }
  }
}
</script>

<style scoped>
.toolbar .tool {
  display: flex;
  position: fixed;
  bottom: 5.3333vw;
  left: 50%;
  right: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  max-width: 200vw;
  min-width: 85.3333vw;
  height: 13.3333vw;
  line-height: 13.3333vw;
  background: #f5f5f5;
}
.toolbar .tool .xz {
  display: flex;
  flex: 1;
}
.toolbar .tool .xz span {
  margin-left: 1.3333vw;
  color: #666666;
  font-size: 3.2vw;
}
.toolbar .tool .btn {
  margin: 4vw 0 0 1.8667vw;
}
.toolbar .tool .price {
  flex: 1;
  color: #666666;
  font-size: 4vw;
}
.toolbar .tool .settlement {
  width: 26.6667vw;
  text-align: center;
  color: #fff;
  background: #ff6b6b;
  font-size: 3.4667vw;
}
</style>