<template>
  <div class="cart">
    <navBar class="nav-header">
      <div class="nav-bars" slot="center">
        <h2>购物车({{ length }})</h2>
      </div>
    </navBar>
    <!-- 商品列表 -->
    <cart-list></cart-list>
    <!-- 底部汇总 -->
    <toolbar></toolbar>
    <section class="null" v-if="length == 0">
      <h2>购物车为空</h2>
    </section>
  </div>
</template>

<script>
import navBar from "components/common/navbar/navbar"
import cartList from "./childcart/cartList"
import toolbar from "./childcart/toolbar"
import { mapGetters } from "vuex"
export default {
  components: {
    navBar,
    cartList,
    toolbar
  },
  computed: {
    // 将vuex映射到计算属性里面
    ...mapGetters({
      length: "cartLength",
      list: "cartList"
    })
  }
}
</script>

<style scoped>
.cart {
  overflow: hidden;
}
.cart .nav-header {
  background: var(--bgcolor);
}
.cart .nav-header h2 {
  font-size: 4.2667vw;
  font-weight: normal;
  color: #fff;
}
.null {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  color: #666666;
  font-size: 5.3333vw;
  font-weight: 400;
}
</style>