<template>
  <section class="cart-list-item">
    <section class="cart-box">
      <btn class="btn" :cheked="cartItem.cheked" @click.native="chenge" />
      <div class="goods-img">
        <img :src="cartItem.image" alt="" />
      </div>

      <div class="content-text">
        <div class="titles">
          <p>{{ cartItem.title }}</p>
        </div>
        <div class="paragraph">
          <p>{{ cartItem.desc }}</p>
        </div>
      </div>

      <div class="bottom-text">
        <div class="price">
          <span>{{ "￥" + cartItem.lowNowPrice }}</span>
        </div>

        <div class="text">
          <button @click="_reduceGoods">-</button>
          <div class="textchange">{{ cartItem.count }}</div>
          <button @click="_addGoods">+</button>
        </div>
      </div>
    </section>
  </section>
</template>

<script>
import btn from "../childcart/btn"
export default {
  props: {
    cartItem: {
      type: Object,
      default() {
        return {}
      }
    }
  },

  data() {
    return {}
  },

  components: {
    btn
  },
  methods: {
    chenge() {
      this.cartItem.cheked = !this.cartItem.cheked
    },

    _addGoods() {
       if(this.cartItem.count >= 10) {
          this.$toast.show("商品数量最多不能超过10", 1500)
      }
      this.$store.commit('addGoods', this.cartItem)
    },

    _reduceGoods() {
       if(this.cartItem.count <= 1) {
          this.$toast.show("商品数量至少为1", 1500)
      }
      this.$store.commit('reduceGoods', this.cartItem)
    }
  }
}

</script>

<style scoped>
.cart-list-item {
  /* max-width: 750px;
  min-width: 320px; */
  margin: 0 auto;
}
.cart-list-item .cart-box {
  position: relative;
  display: flex;
  padding: 2.6667vw 1.8667vw;
  border-bottom: 0.2667vw solid #ccc;
}
.cart-list-item .btn {
  margin-top: 14.6667vw;
}
.cart-list-item .goods-img {
  flex: 1;
  margin: 0 1.8667vw;
}
.cart-list-item .goods-img {
  overflow: hidden;
  width: 32vw;
  height: 34.6667vw;
  -webkit-overflow: hidden;
  border-radius: 50%;
  -webkit-border-radius: 2.6667vw;
}
.cart-list-item .goods-img img {
  width: 100%;
}
.cart-list-item .content-text {
  flex: 2;
}
.cart-list-item .content-text p {
  overflow: hidden;
  font-size: 4.2667vw;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  line-clamp: 1;
  -webkit-box-orient: vertical;
  margin-bottom: 6.6667vw;
  color: #333;
}
.cart-list-item .content-text .paragraph p {
  font-size: 3.4667vw;
  color: #999;
}
.cart-list-item .bottom-text .price {
  position: absolute;
  bottom: 4.5333vw;
  left: 41.3333vw;
  font-size: 4.2667vw;
  color: #e85d04;
}
.cart-list-item .bottom-text .text {
  display: flex;
  position: absolute;
  bottom: 3.2vw;
  right: 4vw;
  border: 0.2667vw solid #ccc;
  height: 8vw;
}
.bottom-text .text button {
  width: 8vw;
  height: 100%;
  background: none;
  font-size: 3.7333vw;
}
.bottom-text .text button:first-child {
  border-right: 0.2667vw solid #ccc;
}
.bottom-text .text button:last-child {
  border-left: 0.2667vw solid #ccc;
}
.bottom-text .text .textchange {
  width: 10.6667vw;
  height: 100%;
  text-align: center;
  font-size: 3.7333vw;
  color: #555;
  line-height: 8vw;
}
</style>