      <!-- 商品列表 -->
      <cartListItem
        v-for="(item, index) in cartList"
        :key="index"
        :cartItem="item"></cartListItem>
    </scroll>
  </div>
</template>

<script>
import cartListItem from "../childcart/cartListItem"
// 第三方
import { mapGetters } from "vuex"
import scroll from "components/common/scroll/scroll"
export default {
  components: {
    scroll,

    cartListItem
  },
  computed: {
    ...mapGetters(["cartList"])
  },
  activated() {
    this.$refs.scroll.scroll.refresh()
  }
}
</script>

<style scoped>
.top-bar-list {
  overflow: hidden;
  height: 100vh;
}
.top-bar-list .contentAll {
  overflow: hidden;
  position: absolute;
